.fixed-plugin .dropdown .dropdown-menu:before,
.fixed-plugin .dropdown .dropdown-menu:after {
    content: "";
    display: inline-block;
    position: absolute;
    top: 46px;
    width: 16px;
    transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
}

.fixed-plugin .dropdown.show .dropdown-menu {
    display: block;
    visibility: visible;
    opacity: 1;
    transform: translateY(-13%);
    transform-origin: 0 0;
}

.fixed-plugin.rtl-fixed-plugin {
    right: auto;
    left: 0px;
    border-radius: 0 8px 8px 0;
}

.fixed-plugin.rtl-fixed-plugin .dropdown-menu {
    right: auto;
    left: 80px;
}

/*создание чекбокс*/

._checkbox {
    display: none;
}

._checkbox-label {
    /*position: absolute;*/
    /*top: 50%;*/
    /*right: 0;*/
    /*left: 0;*/
    width: 50px;
    height: 50px;
    margin: 0 auto;
    background-color: #f72414;
    transform: translateY(-50%);
    border-radius: 50%;
    /*box-shadow: 0 7px 10px #ffbeb8;*/
    cursor: pointer;
    transition: 0.2s ease transform, 0.2s ease background-color, 0.2s ease box-shadow;
    overflow: hidden;
    z-index: 1;
}

._checkbox-label:before {
    content: "";
    position: absolute;
    top: 50%;
    right: 0;

    left: 0;
    width: 20px;
    height: 20px;
    margin: 0 auto;
    background-color: #fff;
    transform: translateY(-50%);
    border-radius: 50%;
    /*box-shadow: inset 0 7px 10px #ffbeb8;*/
    transition: 0.2s ease width, 0.2s ease height;
}

._checkbox-label.blue:before {
    /*box-shadow: inset 0 7px 10px #ffbeb8;*/
}

._checkbox-label:hover:before {
    width: 10px;
    height: 10px;
    /*box-shadow: inset 0 7px 10px #ff9d96;*/
}

._checkbox-label:active {
    transform: translateY(-50%) scale(0.9);
}

.tick_mark {
    position: absolute;
    top: 11px;
    right: 0px;
    left: 3px;
    width: 20px;
    height: 20px;
    margin: 0 auto;
    margin-left: 14px;
    transform: rotateZ(-40deg);
}

.tick_mark:before, .tick_mark:after {
    content: "";
    position: absolute;
    background-color: #fff;
    border-radius: 2px;
    opacity: 0;
    transition: 0.2s ease transform, 0.2s ease opacity;
}

.tick_mark:before {
    left: 0;
    bottom: 0;
    width: 7px;
    height: 25px;
    box-shadow: -2px 0 5px rgba(0, 0, 0, 0.23);
    transform: translateY(-68px)
}

.tick_mark:after {
    left: 0;
    bottom: 0;
    width: 100%;
    height: 7px;
    box-shadow: 0 3px 5px rgba(0, 0, 0, 0.23);
    transform: translateX(78px)
}

